.landing-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f8f9fa;
    text-align: center;
  }
  
  .landing-header {
    font-size: 2.5rem;
    font-weight: bold;
    color: #333;
    margin-bottom: 1.5rem;
  }
  
  .landing-subtext {
    font-size: 1.25rem;
    color: #666;
    margin-bottom: 2rem;
  }
  
  .button-container {
    display: flex;
    justify-content: center;
  }
  
  .btn {
    width: 150px;
    padding: 0.75rem;
    font-size: 1.25rem;
  }
  
  .btn-primary {
    background-color: #007bff;
    border: none;
  }
  
  .btn-primary:hover {
    background-color: #0056b3;
  }
  
  .btn-secondary {
    background-color: #6c757d;
    border: none;
  }
  
  .btn-secondary:hover {
    background-color: #565e64;
  }
  